trategy yang lebih briliant mencuat dari isi kepala pribadi yang lainnya,  dengan menyadari setiap peluang dan kesempatan untuk membuka cakrawala dalam berbisnis, menyatukan setiap ide dan strategy bersama kita yakin kita akan semakin siap untuk menarik setiap peluang terbaik dalam mewujudkan kesuksesan berbisnis dengan income besar yang siap menanti anda.</details>`,\r\n\t\t\t\t\t\tvector: \"https://raw.githubusercontent.com/codesyariah122/bahan-evoush/main/images/vector_image/vector28.jpg\",\r\n\t\t\t\t\t\tanim: \"https://raw.githubusercontent.com/codesyariah122/bahan-evoush/main/images/animated/anim48.gif\"\r\n\t\t\t\t\t},\r\n\t\t\t\t\t{\r\n\t\t\t\t\t\tid: 4,\r\n\t\t\t\t\t\theader: `Management evoush`,\r\n\t\t\t\t\t\tparagraph: `Team management kami yang senantiasa memberikan pelayanan terbaik bagi anda , senantiasa bekerja dengan ketelitian dan keseriusan.<br/> Karena bisnis yang anda bangun di <b>evoush</b> adalah prioritas bagi management kami. <br/> Berikut lengkapnya mengenai management <b>evoush</b> : <br/> <a href=\"{{route('management')}}\" class=\"btn btn-success mt-3 mb-3\">Management Evoush</a>`,\r\n\t\t\t\t\t\tvector: \"https://raw.githubusercontent.com/codesyariah122/bahan-evoush/main/images/vector_image/vector33.jpg\",\r\n\t\t\t\t\t\tanim: \"https://raw.githubusercontent.com/codesyariah122/bahan-evoush/main/images/animated/management1.gif\"\r\n\t\t\t\t\t}\r\n\t\t\t\t],\r\n\r\n\t\t\t\tip:'',\r\n\t\t\t\tcity: ''\r\n\t\t\t}\r\n\t\t},\r\n\t}\r\n</script>\r\n\r\n\r\n<style scoped>\r\n@media only screen and (max-device-width: 812px) {\r\n\t#intents-about{\r\n\t\twidth: 100%!important;\r\n\t\tmargin-top: -1rem;\r\n\t}\r\n\r\n\t#intents-about p{\r\n\t\ttext-indent: 21px;\r\n\t\ttext-align: justify;\r\n\t\tmargin-top: 1rem;\r\n\t\tmargin-left: 1rem;\r\n\t}\r\n\t.intents-about-img{\r\n\t\twidth: 100% !important;\r\n\t\tmargin-left: -.1rem;\r\n\t}\r\n\t.ganjil-img{\r\n\t\twidth: 300px!important;\r\n\t}\r\n\t.anim{\r\n\t\twidth: 300px!important;\r\n\t}\r\n\r\n}