buah investasi yang menjanjikan. Setelah anda bergabung , anda akan menjadi bagian dalam bisnis  Bersama kami.Bersama kami merupakan  pilihan yang tepat. Karena, anda akan memiliki pengalaman baru yang mengesankan dalam dunia bisnis . <br/><b style='font-family: lowercase;'>evoush</b> dengan rangkaian produk kesehatan dan kecantikan yang merupakan sebuah modal lengkap bagi diri dan bisnis anda.`,\r\n\t\t\t\t\tvector: \"https://raw.githubusercontent.com/codesyariah122/bahan-evoush/main/images/animated/anim33.gif\",\r\n\t\t\t\t\tcarousel: false\r\n\t\t\t\t}\r\n\t\t\t\t]\r\n\t\t\t}\r\n\t\t},\r\n\r\n\t\tbeforeMount(){\r\n\t\t\tthis.getIp(),\r\n\t\t\tthis.getLocation(this.ip)\r\n\t\t},\r\n\t\tmethods: {\r\n\t\t\tgetIp(){\r\n\t\t\t\tthis.axios\r\n\t\t\t\t.get('https://api.ipify.org/?format=json')\r\n\t\t\t\t.then(res => {\r\n\t\t        \t// console.log(res)\r\n\t\t        \tthis.ip = res.data.ip\r\n        \t\t})\r\n        \t\t.catch(err => console.log(err.message))\r\n\t\t\t},\r\n\t\t\tgetLocation(ip){\r\n\t\t\t\tthis.axios\r\n\t\t\t\t.get(`https://ipapi.co/${ip}/json/`)\r\n\t\t\t\t.then(res => {\r\n\t\t\t\t\tthis.city = res.data.city\r\n\t\t\t\t})\r\n\t\t\t\t.catch(err => console.log(err.message))\r\n\t\t\t}\r\n\t\t}\r\n\t\t\r\n\t}\r\n</script>\r\n\r\n\r\n<style scoped>\r\n@media only screen and (max-device-width: 812px) {\r\n\t#intents-home{\r\n\t\twidth: 100%!important;\r\n\t\tmargin-top: -1rem;\r\n\t}\r\n\r\n\t#panel-intents-home p{\r\n\t\ttext-indent: 21px;\r\n\t\ttext-align: justify;\r\n\t\tmargin-top: 1rem;\r\n\t\tmargin-left: 1rem;\r\n\t}\r\n\t#panel-intents-home h1{\r\n\t\ttext-transform: uppercase;\r\n\t}\r\n\t#panel-intents-home video{\r\n      width:350px!important;\r\n      margin-top: -1rem!important;\r\n    }\r\n\t.intents-home-img{\r\n\t\twidth: 300px !important;\r\n\t\theight: 300px !important;\r\n\t\tmargin-left: -.1rem;\r\n\t}\r\n\r\n}\r\n/* DESKTOP VERSION */\r\n@media (min-width: 992px) { \r\n\t#panel-intents-home{\r\n\t\twidth: 100%!important;\r\n\t}\r\n\t#panel-intents-home p{\r\n\t\tfont-family: 'Poiret One';\r\n\t\tfont-size: 21px!important;\r\n\t\tmargin-top: 1rem!important;\r\n\t\tmargin-left: 1.3rem;\r\n\t\ttext-align: justify;\r\n\t\ttext-indent: 18px;\r\n\t}\r\n\r\n\t#panel-intents-home h1{\r\n\t\ttext-transform: uppercase;\r\n\t}\r\n\t#panel-intents-home video{\r\n      width:650px!important;\r\n      margin-top: -1rem!important;\r\n    }\r\n\t.genap-home {\r\n\t\tmargin-top: 3rem;\r\n\t\t/*margin-bottom: 3rem;*/\r\n\t}\r\n\r\n\t.genap-home img{\r\n\t\twidth: 800px !important;\r\n\t\tmargin-left: -7rem;\r\n\t}\r\n}\r\n</style>>"],"sourceRoot":""}]);
